body,
h1,
h2,
h3,
h4,
p,
footer,
hr,
ul,
input,
form,
button {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  font-style: normal;
}

form {
  height: 100%;
}

input {
  border: 0;
}

.header {
  min-width: 320px;
  height: 72px;
  padding: 0 50px;
  box-shadow: 0 0 16px rgba(38, 38, 38, 0.16);
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 40px;
}
.header__image {
  width: 120px;
}
.header__buttons-box {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  height: 100%;
  margin-left: 92px;
}
.header__buttons-box__single-button {
  font-weight: 500;
  font-size: 13px;
  line-height: 24px;
  height: 40px;
  width: 80px;
  background-color: #262626;
  color: #fff;
  border-radius: 2px;
  border: 0;
}
.header__buttons-box__single-button:not(:last-of-type) {
  margin-right: 16px;
}
.header__buttons-box__single-button:hover {
  background-color: #fff;
  color: #1d1d1d;
  outline: 1px solid #e0e0e0;
  cursor: pointer;
}

.box-time-place {
  height: 40px;
  display: flex;
  align-items: center;
}
.box-time-place__time {
  height: 40px;
  width: 65px;
  text-align: center;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #1d1d1d;
  border: 1px solid #e0e0e0;
  border-radius: 2px;
}
.box-time-place__between-inputs {
  margin: 0 9px;
  font-weight: 300px;
  font-size: 16px;
  line-height: 19px;
  color: #626262;
}
.box-time-place__place {
  width: 292px;
  display: flex;
  align-items: center;
  border: 1px solid #e0e0e0;
  border-radius: 2px;
  height: 100%;
  box-sizing: border-box;
}
.box-time-place__place__icon-geo {
  margin: 0 16px;
}
.box-time-place__place__input {
  display: flex;
  align-items: center;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #1d1d1d;
}

.search {
  min-width: 320px;
  padding: 0 50px;
}
.search__box-input {
  display: flex;
  margin-bottom: 15px;
}
.search__box-input__image {
  margin-right: 8px;
}
.search__box-input__input {
  width: 260px;
  height: 24px;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  color: #626262;
}
.search__bottom-line {
  color: #757575;
  opacity: 0.3;
  margin-bottom: 25px;
}

.mobile-box-place {
  display: none;
}

@media (min-width: 640px) and (max-width: 1023px) {
  .header {
    padding: 0 8px;
  }
  .header__image {
    width: 110px;
  }
  .header__buttons-box {
    margin-left: 0;
  }
  .header__buttons-box__single-button {
    width: 72px;
  }
  .header__buttons-box__single-button:not(:last-of-type) {
    margin-right: 12px;
  }

  .search {
    padding: 0 8px;
  }

  .box-time-place__time {
    width: 57px;
    font-size: 13px;
    line-height: 24px;
  }
  .box-time-place__between-inputs {
    margin: 0 7px;
    font-weight: normal;
    font-size: 13px;
    line-height: 15px;
  }
  .box-time-place__place {
    width: 215px;
  }
  .box-time-place__place__icon-geo {
    margin: 0 10px;
  }
  .box-time-place__place__input {
    font-size: 13px;
    line-height: 24px;
  }
}
@media (max-width: 639px) {
  .header {
    padding: 0 8px;
  }
  .header__image {
    width: 105px;
  }
  .header__buttons-box {
    margin-left: 0;
  }
  .header__buttons-box__single-button {
    width: 72px;
    font-weight: 500;
    font-size: 11px;
    line-height: 13px;
  }
  .header__buttons-box__single-button:not(:last-of-type) {
    margin-right: 12px;
  }

  .search {
    padding: 0 8px;
  }
  .search__box-input__image {
    width: 16px;
    height: 16px;
  }
  .search__box-input__input {
    width: 210px;
    font-size: 13px;
  }

  .icon-basket {
    display: none;
  }

  .mobile-box-place {
    display: block;
    width: 308px;
    height: 61px;
    padding: 0 8px;
  }

  .box-time-place {
    height: 40px;
    display: flex;
    align-items: center;
    font-size: 11px;
    line-height: 13px;
    color: #626262;
  }
  .box-time-place__time {
    height: 40px;
    width: 65px;
    text-align: center;
    font-weight: normal;
    font-size: 13px;
    line-height: 24px;
    color: #1d1d1d;
    border: 1px solid #e0e0e0;
    border-radius: 2px;
    margin-right: 16px;
    margin-top: 8px;
  }
  .box-time-place__between-inputs {
    display: none;
  }
  .box-time-place__place {
    width: 227px;
    display: flex;
    align-items: center;
    border: 1px solid #e0e0e0;
    border-radius: 2px;
    height: 100%;
    box-sizing: border-box;
    margin-top: 8px;
  }
  .box-time-place__place__icon-geo {
    margin: 0 16px;
  }
  .box-time-place__place__input {
    display: flex;
    align-items: center;
    font-weight: normal;
    font-size: 13px;
    line-height: 24px;
    color: #1d1d1d;
  }

  .header .box-time-place {
    display: none;
  }
}
.info-place {
  margin: 55px 50px 16px;
  font-size: 22px;
  line-height: 34px;
  color: #1d1d1d;
  font-weight: normal;
}

.block-cards {
  min-width: 320px;
  padding: 0 50px;
  display: grid;
  column-gap: 12px;
  row-gap: 15px;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  margin-bottom: 100px;
}
.block-cards__card {
  width: 100%;
}
.block-cards__card__img {
  width: 100%;
  margin-bottom: 15px;
}
.block-cards__card__title {
  font-size: 16px;
  line-height: 24px;
  color: #1d1d1d;
  font-weight: normal;
  font-style: normal;
  margin-bottom: 4px;
}
.block-cards__card__category {
  font-size: 14px;
  line-height: 21px;
  color: #626262;
  font-weight: normal;
  font-style: normal;
  margin-bottom: 4px;
}
.block-cards__card__time-info {
  font-size: 13px;
  line-height: 21px;
  color: #1d1d1d;
  font-weight: normal;
  font-style: normal;
  margin-bottom: 4px;
}

@media (min-width: 640px) and (max-width: 1023px) {
  .block-cards {
    padding: 0 8px;
    grid-template-columns: repeat(2, minmax(306px, 497px));
  }

  .info-place {
    margin: 55px 8px 16px;
  }
}
@media (max-width: 639px) {
  .block-cards {
    padding: 0 8px;
    grid-template-columns: repeat(1, minmax(300px, 623px));
  }

  .info-place {
    margin: 55px 8px 16px;
  }
}
.footer {
  position: relative;
  background-color: #262626;
  padding: 50px 50px 40px;
}
.footer__img {
  padding-bottom: 40px;
}
.footer__line {
  width: 100%;
  opacity: 0.4;
  height: 1px;
  background-color: #fff;
  margin-bottom: 32px;
}
.footer__menu {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 50px;
}
.footer__menu__menu-left__box-selection {
  display: flex;
  align-items: center;
  border: 1px solid #3b3b3b;
  background-color: #3b3b3b;
  padding: 0;
  margin-bottom: 30px;
  width: 227px;
}
.footer__menu__menu-left__box-selection__img {
  margin: 0 20px;
}
.footer__menu__menu-left__box-selection__selection {
  background-color: #3b3b3b;
  width: 227px;
  height: 40px;
  color: #fff;
  font-size: 13px;
  line-height: 15px;
  font-style: normal;
  border: 0;
}
.footer__menu__menu-left__hashtag {
  font-size: 16px;
  line-height: 19px;
  color: #fff;
  text-decoration: none;
}
.footer__menu__menu-left__hashtag::before {
  content: "#";
  color: #49a144;
  font-size: 16px;
  line-height: 19px;
}
.footer__menu__menu-left__hashtag-conteiner {
  margin-bottom: 20px;
}
.footer__menu__menu-left__network-icons__icon {
  margin-right: 15px;
}
.footer__icons-apps {
  display: flex;
  justify-content: center;
  padding-bottom: 40px;
}
.footer__icons-apps__icon:first-of-type {
  margin-right: 40px;
}
.footer__terms {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.footer__terms > a {
  font-size: 11px;
  line-height: 13px;
  color: #fff;
  text-decoration: none;
}
.footer__button-to-top {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: 15px;
  bottom: 25px;
  width: 80px;
  height: 80px;
  background-color: #59bd5a;
  border-radius: 2px;
}

.menu-list {
  list-style: none;
  padding: 0;
}
.menu-list__li:not(:last-of-type) {
  margin-bottom: 23px;
}
.menu-list__item {
  color: #fff;
  font-size: 13px;
  line-height: 15px;
  text-decoration: none;
}

@media (min-width: 640px) and (max-width: 1023px) {
  .footer {
    padding: 50px 8px 40px;
  }
  .footer__menu__menu-left__box-selection {
    width: 176px;
  }
}
@media (max-width: 639px) {
  .footer {
    padding: 50px 8px 40px;
  }
  .footer__menu {
    display: grid;
    grid-template-columns: 1fr;
    margin-bottom: 40px;
  }
  .footer__menu__menu-middle {
    margin-bottom: 23px;
  }
  .footer__menu__menu-left {
    margin-bottom: 40px;
  }
  .footer__menu__menu-left__box-selection {
    width: 173px;
  }
  .footer__terms {
    grid-template-columns: 1fr;
    grid-row-gap: 24px;
  }
  .footer__button-to-top {
    width: 64px;
    height: 64px;
  }
}

